.main-page {
  flex-grow: 1; // sticky footer
}

.main-page__container {
  width: @mobile-width;
  margin: 0 auto;
}

.main-page__intro {
  margin-top: 27px;
  margin-bottom: 20px;
}

.main-page__program.program { //можно так повысить специфичность?
  margin-bottom: 20px;
}

.main-page__title {
  font-weight: 400;
  font-size: 36px;
  line-height: 1;
  margin: 0;
  padding: 27px 20px 41px 20px;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #ebebeb;
}

.main-page__title--form {
  border-bottom: none;
  padding-bottom: 37px;
}

.main-page__description {
  width: @mobile-width;
  margin: 0 auto;
  margin-bottom: 39px;
  padding-right: 30px;
  font-size: 14px;
  line-height: 1.28;
  text-transform: uppercase;
}
